.containerHeader {
    margin-top: 15px;
    border-radius: 5px;
    width: 744px;
    height: 110px;
    background-color: white;
    display: flex;

    .pointDev {
        margin-left: 400px;

        .point {
            float: right;
            width: 34px;
            height: 34px;
            left: 0;
        }
    }

    h1 {
        font-size: 16px;
        padding-top: 15px;
    }

    p {
        font-size: 12px;
        padding-right: 130px;
    }

    .man {
        padding: 10px;
    }
}

.sidebar {
    margin-right: 40px;
    width: 366px;
    background: #F1F2F3;
    border-radius: 5px;

    .search {
        display: flex;
        padding: 10px;
        margin-left: 10px;
        background-color: white;
        border-radius: 10px;
        margin-bottom: 15px;
        margin-top: 15px;
        -webkit-box-shadow: 54px 32px 34px -21px rgba(34, 60, 80, 0.2);
-moz-box-shadow: 54px 32px 34px -21px rgba(34, 60, 80, 0.2);
box-shadow: 54px 32px 34px -21px rgba(34, 60, 80, 0.2);
        .lup{
            margin-right: 230px;
        }
        .two{
            display: flex;
            gap: 8px;
        }
    }

    .upCount{
        display: flex;
        margin-bottom: 5px;
        span{
            margin-left: 20px;
            border: 1px solid #616F82;
            color: white;
            border-radius: 20px;
            padding: 1px;
            font-size: 12px;
            background-color: #616F82;
        }

        p{
            padding-left: 240px;
            font-size: 14px;
            color: #616F82;
        }
    }

    .cartItem {
        display: flex;
        align-items: center;
        border: none;
        background-color: white;
        border-radius: 10px;
        margin: 10px;
        -webkit-box-shadow: 54px 32px 34px -21px rgba(34, 60, 80, 0.2);
-moz-box-shadow: 54px 32px 34px -21px rgba(34, 60, 80, 0.2);
box-shadow: 54px 32px 34px -21px rgba(34, 60, 80, 0.2);

        img {
            width: 40px;
        }

        p {
            padding-left: 10px;
            padding-top: 20px;
            padding-bottom: 20px;
        }

    }

    .cartItem:hover {
        background: #F1F2F4
    }
}

.type {
    display: flex;
    text-align: center;
    padding-left: 5px;
    padding-top: 15px;
    padding-bottom: 10px;

    p:hover{
        color: #4198C5;
    }

    span {
        padding: 0 10px;
    }

    .btn {
        display: flex;

        .pi {
            padding-right: 10px;
            padding-left: 231px;
        }
    }

    

}

.contentCards {
    height: 740px;
    background-color: white;
    padding: 5px 10px;
    border-radius: 5px;

    .containerContent {
        padding-bottom: 10px;
        padding-top: 5px;

        .cardCalling {
            padding: 2px;
            border: 1px solid #4c8aaa96;
            border-radius: 5px;
            gap: 10px;
            display: flex;

            img {
                width: 35px;
                height: 45px;
                padding-top: 10px;
            }

            .content {
                p {
                    padding-bottom: 10px;
                }

                span {
                    font-size: 15px;
                    color: #616F82;
                }
            }
        }

        .cardCalling:hover {
            opacity: 0.8;
        }
    }
}