.pizza-footer {
    border-top: 1px solid #f6f6f6;
    ;
    padding: 20px 20px;
    align-items: center;
    display: flex;
    justify-content: center;

    span {
        font-weight: 600;
        font-size: 1.5em;
    }

    .block {
        transition: 500ms ease-in-out;
        p {
            padding-left: 0.5em;
            cursor: pointer;
        }

        
        &__me {
            margin-left: 1em;
            border-radius: 10px;
            -webkit-box-shadow: 0px 5px 10px 2px rgba(0, 0, 0, 0.2) inset;
            -moz-box-shadow: 0px 5px 10px 2px rgba(0, 0, 0, 0.2) inset;
            box-shadow: 0px 5px 10px 2px rgba(0, 0, 0, 0.2) inset;
            padding: 10px 0 10px 0;
            
            li{
                padding: 10px;
                text-align: center;

                &.active,
                &:hover{
                    background-color: rgba(249, 224, 205, 0.439);
                    color: #fe5d1e82;
                }

                &.active {
                    background-color: rgba(249, 224, 205, 0.439);
                    color: #fe5f1e;
                    font-weight: bold;
                }
                
            }
        }
    }


}